import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";

import Layout from "./Layout";
import Badges from "../pages/Badges";
import BadgeNew from "../pages/BadgeNew";
import BadgeEdit from "../pages/BadgeEdit";
import NotFound from "../pages/NotFound";
import BadgeDetailsContainer from "../pages/BadgeDetailsContainer";
import Home from "../pages/Home";

function App() {
  return (
    <BrowserRouter>
      <Layout>
        <Switch>
          <Route exact path="/reactjs_fundamentals/" component={Home} />
          <Route exact path="/reactjs_fundamentals/badges" component={Badges} />
          <Route exact path="/reactjs_fundamentals/badges/new" component={BadgeNew} />
          <Route exact path="/reactjs_fundamentals/badges/:badgeId" component={BadgeDetailsContainer} />
          <Route exact path="/reactjs_fundamentals/badges/:badgeId/edit" component={BadgeEdit} />
          <Route component={NotFound} />
        </Switch>
      </Layout>
    </BrowserRouter>
  );
}

export default App;
